import { Component, OnInit } from '@angular/core';
import {Http} from "@angular/http";
import "rxjs/add/operator/map";
import {BiensService} from "../../services/biens.service";
import {Router} from "@angular/router";
import {Bien} from "../../model/model.biens";

@Component({
  selector: 'app-biens',
  templateUrl: './biens.component.html',
  styleUrls: ['./biens.component.css']
})
export class BiensComponent implements OnInit {

  pageBiens:any;
  motCle1:string="";
  motCle2:string="";
  currentPage:number=0;
  size:number=5;
  pages:Array<number>;

  constructor(private http:Http, private biensService:BiensService,
              public router:Router) {

  }

  ngOnInit() {

  }

  doSearch() {
    this.biensService.getBiens(this.motCle1, this.motCle2, this.currentPage, this.size)
      .subscribe(data=>{
        this.pageBiens = data;
        this.pages = new Array(data.totalPages)
      }, error=>{
        console.log(error());
      })
  }

  chercher() {
    this.doSearch()
  }

  goToPage(i:number) {
    this.currentPage = i;
    this.doSearch();
  }

  onEditBien(id:number) {
    this.router.navigate(['edit-bien',id])
  }

  onDeleteBien(b:Bien) {
    let confirm = window.confirm('Êtes-vous sûr de vouloir supprimer ce bien?');
    if(confirm == true) {
      this.biensService.deleteBien(b.idBien)
        .subscribe(data=>{
          this.pageBiens.content.splice(
            this.pageBiens.content.indexOf(b), 1
          );
          alert("Suppression Ok!")
        },err=>{
          console.log(err());
        })
    }

  }

}



