import { Component, OnInit } from '@angular/core';
import {Bien} from "../../model/model.biens";
import {ActivatedRoute, Router} from "@angular/router";
import {BiensService} from "../../services/biens.service";

@Component({
  selector: 'app-edit-bien',
  templateUrl: './edit-bien.component.html',
  styleUrls: ['./edit-bien.component.css']
})
export class EditBienComponent implements OnInit {

  mode:number = 1;
  bien:Bien = new Bien();
  idBien:number;

  constructor(public activatedRoute:ActivatedRoute, public biensService:BiensService, public router:Router) {
    this.idBien = activatedRoute.snapshot.params['id'];
  }

  ngOnInit() {
    this.biensService.getBien(this.idBien)
      .subscribe(data=>{
        this.bien = data;
      }, err=>{
        console.log(err);
      })
  }

  updateBien() {
    this.biensService.updateBien(this.bien)
      .subscribe(data=>{
        this.bien = data;
        this.router.navigate(['biens'])
      }, err=>{
        console.log(err);
      })
  }
}



