import { Component, OnInit } from '@angular/core';
import {BiensService} from "../../services/biens.service";

@Component({
  selector: 'app-new-bien-two',
  templateUrl: './new-bien-two.component.html',
  styleUrls: ['./new-bien-two.component.css']
})
export class NewBienTwoComponent implements OnInit {

  constructor(public bienService:BiensService) { }

  ngOnInit() {
  }

  onSaveBien(dataForm) {
    this.bienService.saveBien(dataForm)
      .subscribe(data=>{
        console.log(data);
      }, err=>{
        console.log(JSON.parse(err._body).message);
      })
  }
}



