import { Component, OnInit } from '@angular/core';
import {Bien} from "../../model/model.biens";
import {BiensService} from "../../services/biens.service";

@Component({
  selector: 'app-new-bien',
  templateUrl: './new-bien.component.html',
  styleUrls: ['./new-bien.component.css']
})
export class NewBienComponent implements OnInit {

  bien:Bien = new Bien();
  mode:number=1;

  constructor(public bienService:BiensService) { }

  ngOnInit() {

  }

  saveBien() {
    this.bienService.saveBien(this.bien)
      .subscribe(data=>{
        this.bien = data;
        this.mode = 2;
      }, err=>{
        console.log(err)
      })
  }

}



