import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { BiensComponent } from './biens/biens.component';
import {Routes, RouterModule} from "@angular/router";
import { AboutComponent } from './about/about.component';
import {HttpModule} from "@angular/http";
import {BiensService} from "../services/biens.service";
import {FormsModule} from "@angular/forms";
import { NewBienComponent } from './new-bien/new-bien.component';
import { NewBienTwoComponent } from './new-bien-two/new-bien-two.component';
import { EditBienComponent } from './edit-bien/edit-bien.component';


const appRoutes:Routes = [
  {path:'about', component:AboutComponent},
  {path:'biens', component:BiensComponent},
  {path:'new-bien', component:NewBienComponent},
  {path:'edit-bien/:id', component:EditBienComponent},
  {path:'', redirectTo:'/about',pathMatch:'full'}
];

@NgModule({
  declarations: [
    AppComponent,
    BiensComponent,
    AboutComponent,
    NewBienComponent,
    NewBienTwoComponent,
    EditBienComponent
  ],
  imports: [
    BrowserModule, RouterModule.forRoot(appRoutes), HttpModule, FormsModule
  ],
  providers: [BiensService],
  bootstrap: [AppComponent]
})
export class AppModule { }



